<template>
    <div>
        <div style="height: 100%; width: 100%; position: absolute; margin-top: 0; margin-bottom: 0; display: flex; justify-content: center; align-items: center">
            <div style="width: 700px; height: 620px; background-color: yellow; border-radius: 10px; border: 1px solid #bbbbbb">
                <div style="height: 30px; background-color: #409eff; padding-left: 10px; border-top-left-radius: 10px; border-top-right-radius: 10px;">Echo</div>
                <div style="height: 420px; background-color: #d3dce6; overflow: auto" id="ggg">
                    <div v-for="(item, index) in msgList" :key="index">
                        <div v-if="item.fromSelf" style="display: flex; margin: 10px 5px; justify-content: flex-end">
                            <div style="border-radius: 10px; border: 1px solid #9093c7; width: 70%; padding: 10px; margin: 0 10px">{{item.data}}</div>
                            <div>
                                <el-image
                                        style="width: 50px; height: 50px; border-radius: 100%"
                                        :src="'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg'"
                                        fit="cover"></el-image>
                            </div>
                        </div>
                        <div v-else style="display: flex; margin: 10px 5px; justify-content: flex-start">
                            <div>
                                <el-image
                                        style="width: 50px; height: 50px; border-radius: 100%"
                                        :src="'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg'"
                                        fit="cover"></el-image>
                            </div>
                            <div style="border-radius: 10px; border: 1px solid #9093c7; width: 70%; padding: 10px; margin: 0 10px">{{item.data}}</div>
                        </div>
                    </div>
                </div>
<!--                <div style="height: 30px; background-color: #cf9236">-->
<!--                    这里放点工具啥的, 上传图片文件截图消息记录这种-->
<!--                </div>-->
                <div style="height: 160px;">
                    <el-input
                            type="textarea"
                            :rows="7"
                            resize="none"
                            @keydown.enter.native="sendMsg"
                            v-model="toDoSendMsg">
                    </el-input>
                </div>
                <div style="height: 35px; background-color: #409eff; border-bottom-left-radius: 10px; border-bottom-right-radius: 10px;">
                    <el-button type="info" style="height: 100%; float: right" @click="sendMsg">发送</el-button>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "Echo",
        data() {
            return {
                toDoSendMsg: '',
                msgList: [],
            }
        },
        methods: {

            sendMsg() {
                if (!this.toDoSendMsg || this.toDoSendMsg.trim() === '') {
                    this.$message({
                        message: '发送消息不能为空',
                        type: 'warning'
                    });
                    this.toDoSendMsg = '';
                    return;
                }
                this.msgList.push({
                    id: 1,
                    type: 1,
                    fromSelf: true,
                    data: this.toDoSendMsg,
                    createTime: '2021-12-23 23:10:00'
                });
                let msg = this.toDoSendMsg;
                this.toDoSendMsg = '';
                setTimeout(() => {
                    let c = this.$el.querySelector("#ggg");
                    c.scrollTop = c.scrollHeight + 1000;
                    this.msgList.push({
                        id: 1,
                        type: 1,
                        fromSelf: false,
                        data: msg.split("").reverse().join(""),
                        createTime: '2021-12-23 23:10:00'
                    });
                    setTimeout(() => {
                        c = this.$el.querySelector("#ggg");
                        c.scrollTop = c.scrollHeight + 1000;
                    }, 50);
                }, 300);


            },
        }
    }
</script>

<style scoped>

</style>
